.song-container {
  min-height: 60vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  img {
    width: 15%;
    border-radius: 50%;
    object-fit: cover;
  }
  img:hover {
    opacity: 0.8;
    cursor: pointer;
  }
  h2 {
    padding: 3rem 1rem 1rem 1rem;
  }
  h5 {
    font-size: 1rem;
  }
}
.rotate {
  animation: rotation 20s infinite linear;
}
@keyframes rotation {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(359deg);
  }
}
@media screen and (max-width: 768px) {
  .song-container {
    img {
      width: 60%;
    }
  }
}
